import { EIntOrd } from '../Order/ExtendedInt';
import { Field } from "./Field";
// A field with a discrete valuation.
export class DVField extends Field {
    inValuationRing(a) {
        return EIntOrd.gte(this.valuation(a), 0);
    }
    isIntegerUnit(a) {
        return this.valuation(a) === 0;
    }
}
