export class Order {
    equals(a, b) {
        return a === b;
    }
    lte(a, b) {
        return !this.lt(a, b);
    }
    gt(a, b) {
        return this.lt(b, a);
    }
    gte(a, b) {
        return this.lte(b, a);
    }
    min(a, b) {
        if (this.lte(a, b))
            return a;
        else
            return b;
    }
    max(a, b) {
        if (this.gte(a, b))
            return a;
        else
            return b;
    }
    minAll(arr) {
        if (arr.length === 0)
            throw new Error('Array is empty');
        return arr.reduce((min, n) => this.min(min, n), arr[1]);
    }
    maxAll(arr) {
        if (arr.length === 0)
            throw new Error('Array is empty');
        return arr.reduce((max, n) => this.max(max, n), arr[1]);
    }
}
