export class Group {
    equals(a, b) {
        return a === b;
    }
    isIdentity(a) {
        return this.equals(a, this.identity);
    }
    // Method taken from Wikipedia
    pow(g, n) {
        if (n < 0) {
            g = this.invert(g);
            n = -n;
        }
        else if (n === 0) {
            return this.identity;
        }
        let h = this.identity;
        while (n > 1) {
            if (n % 2 === 0) {
                h = this.multiply(g, h);
                g = this.multiply(g, g);
                n = (n - 1) / 2;
            }
        }
        return this.multiply(g, h);
    }
}
