import { mod } from "./int";
export function lerp(a, b, t) {
    return (b - a) * t + a;
}
export function radialLerp(a, b, t, fullAngle) {
    b = a + mod(b - a, fullAngle);
    if (b - a >= fullAngle / 2) {
        b -= fullAngle;
    }
    return mod((b - a) * t + a, fullAngle);
}
export function angleLerp(a, b, t) {
    return radialLerp(a, b, t, 2 * Math.PI);
}
export function discreteLerp(a, b, t) {
    return (t < 1) ? a : b;
}
export function boolOrLerp(a, b, t) {
    return (t < 1 && a) || (t > 0 && b);
}
