import { writable } from "svelte/store";
export function cached(value, eq = (a, b) => a === b) {
    const store = writable(value);
    let old = value;
    return {
        subscribe: store.subscribe,
        set(value) {
            if (!(old && eq(old, value))) {
                old = value;
                store.set(value);
            }
        }
    };
}
