import { Group } from "./Group";
export class FieldMultiplicativeGroup extends Group {
    constructor(field) {
        super();
        this._field = field;
    }
    get field() {
        return this._field;
    }
    get identity() {
        return this.field.one;
    }
    equals(a, b) {
        return this.field.equals(a, b);
    }
    isIdentity(a) {
        return this.field.isOne(a);
    }
    multiply(a, b) {
        return this.field.multiply(a, b);
    }
    invert(a) {
        return this.field.invert(a);
    }
}
