import { Group } from "../Group/Group";
export class RingAdditiveGroup extends Group {
    constructor(field) {
        super();
        this._ring = field;
    }
    get ring() {
        return this._ring;
    }
    get identity() {
        return this._identity;
    }
    multiply(a, b) {
        return this.ring.add(a, b);
    }
    invert(a) {
        return this.ring.negate(a);
    }
}
